  video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
  }

  .hero-container {
    /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }

  .hero-container > h1 {
    color: #fff;
    font-size: 5rem;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
    margin: 2rem auto 1rem auto;
    opacity: 0; /* Start with both elements hidden */
    transition: opacity 5s ease-in-out;
    text-align: center;
    position: absolute; /* Position each element absolutely */
    top: 30%; /* Center vertically */
    left: 50%; /* Center horizontally */
    transform: translate(-50%, -50%); /* Adjust to perfectly center */
  }

  .hero-container > h1 {
    animation: fadeH1 5s ease-in-out forwards;
  }


  @keyframes fadeH1 {
    0% { opacity: 0; }
    65% { opacity: 0.65; }
    100% { opacity: 0.65; }
  }

  @keyframes fadeP {
    0% { opacity: 0; }
    25% { opacity: 1; }
    55% { opacity: 1; }
    100% { opacity: 0; }
  }

  @keyframes fadeP3 {
    0% { opacity: 0; }
    25% { opacity: 1; }
    55% { opacity: 1; }
    100% { opacity: 1; }
  }
    
  .hero-btns {
    margin-top: 2rem;
    padding: 6px 28px; /* Padding inside the button */
    background: rgba(0, 0, 0, 0.4); /* Transparent background to show progress fill */
    color: #fff; /* Text color */
    opacity: 0.8;
    border: 2px solid rgba(255, 255, 255, 0.8); ; /* Light border to make the button pop */
    border-radius: 4px; /* Rounded corners */
    cursor: pointer; /* Pointer cursor on hover */
    font-size: 30px; /* Font size */
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
    position: absolute; /* Position each element absolutely */
    top: 47%; /* Center vertically */
    left: 50%; /* Center horizontally */
    transform: translate(-50%, -50%); /* Adjust to perfectly center */
    overflow: hidden; /* Hide overflow of progress-fill */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5); /* Shadow for depth */
    transition: background-color 0.3s, border-color 0.3s, transform 0.3s; /* Smooth transitions for hover effects */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8); /* Text shadow for better readability */
  }

    .hero-btns:hover {
      border-color: rgba(255, 255, 255, 0.8); /* Brighter border color on hover */
      transform: translate(-50%, -50%) scale(1.1);
      opacity: 0.75;
    }
    
    .fa-play-circle {
      margin-left: 4px;
    }
    
    @media screen and (max-width: 960px) {
      .hero-container > h1 {
        font-size: 70px;
        margin-top: -150px;
      }
    }
    
    @media screen and (max-width: 768px) {
      .hero-container > h1 {
        font-size: 50px;
        margin-top: -100px;
      }
    
      .hero-container > p {
        font-size: 30px;
      }
    
      .btn-mobile {
        display: block;
        text-decoration: none;
      }
    
      .btn {
        width: 100%;
      }
    }