
html, body {
  --font-primary: "Roboto",Helvetica Neue,Helvetica,sans-serif;
  --font-secondary: "Montserrat",Helvetica Neue,Helvetica,sans-serif;
  font-family: var(--font-primary);
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
    overscroll-behavior: none; /* Disables the scroll bounce effect */
  margin: 0;
  padding: 0;
  height: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Full viewport height */
}

.main-content {
  min-height: 91vh;
}


.main-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
}

.main-page > h1 {
  background: #fff;
  padding: 1% 3%;
  border: 1px solid black;
}

.main-page > p {
  padding: 1rem;
  width: 108ch;
  background: #fff;
  border: 0.5px solid black;
  border-radius: 5px;
}

.cities {
  width: 90vw;
  height: 40vh;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  
}