.card {
    display: flex;
    flex-direction: row;    
    flex: 1;
    border-radius: 5px;
    width: 100%;
    margin: 1.5rem;
    border: 0.5px solid black;
    position: relative; /* Establishes a new stacking context */
  }
  
  .card__link {
    display: flex;
    flex-direction: row;
    width: 40%;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15); /* Subtle shadow with slight blur */
    -webkit-filter: drop-shadow(0 4px 10px rgba(0, 0, 0, 0.15));
    filter: drop-shadow(0 4px 10px rgba(0, 0, 0, 0.15));
    overflow: hidden;
    text-decoration: none;
  }
  
  .card__img-wrap {
    position: relative;
    width: 100%;
    margin: 0;
    padding: 0;
  }

  .card__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  
  .card__link:hover .card__img {
  }
  
  .card__info {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width: 60%;
    height: auto;
    padding: 1rem 1rem 1rem 3rem;
    border-radius: 5px;
  }
  
  .destination {
    font-family: var(--font-secondary);
    margin: 0 0 1rem 0;
    color: rgb(18, 99, 211);
    text-align: left;
    font-size: 1.75rem;
    font-weight: bolder;
    text-decoration: none;
  }

  .destination:hover {
    color: black;
  }
  
  .description,
  .attractions {
    margin: 0 0 1rem 0;
    padding: 0;
    color: #252e48;
    font-size: 16px;
    text-align: left;
  }
  
  @media only screen and (min-width: 1200px) {
    .card {
      width: 84%;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .card {
      margin-bottom: 2rem;
    }
  }
  