.navbar {
  font-family: var(--font-secondary);
  font-weight: 470;
  height: 9vh;
  width: 100vw;
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: inherit;
  width: 100vw;
}

.navbar-logo {
  color: #fff;
  justify-content: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 15px;
  list-style: none;
  text-align: center;
  width: 85vw;
  justify-content: end;
  padding: 0;
  margin: 0;
}

.nav-item {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center;     /* Center vertically */
  list-style: none;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: 1.3rem;
  padding: 0.25rem 0.5rem;
  height: 100%;
  transition: transform 0.3s ease, text-shadow 0.3s ease; /* Smooth transition for text shadow */
}

.nav-links:hover {
  transform: scale(1.10); /* Scale up the link on hover */
}

.nav-item.active {
  text-shadow: 0 0 3px rgba(255, 255, 255, 0.7), 0 0 6px rgba(255, 255, 255, 0.5); /* Subtle text shadow to indicate active link */
  transform: none; /* Ensure active link does not scale up */
  background-color: transparent; /* Ensure active link background is not affected */
  text-decoration: underline  ;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  cursor: pointer;
  color: #fff; /* Adjust color to ensure visibility */
  scale: 1.4;
  padding-left: 0.5%;
}

.menu-icon:hover {
  transform: scale(1.10); /* Scale up the link on hover */
}

/* Dropdown menu container */
.nav-item.dropdown {
  position: relative; /* Ensure the dropdown is positioned relative to its parent */
}

/* Hidden dropdown content by default */
.dropdown-content {
  /* Flexbox to center items */
  display: flex;
  flex-direction: column;
  align-items: center; /* Center horizontally */
  justify-content: space-between;
  width:inherit;
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  position: absolute;
  top: 6.7vh;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-bottom-left-radius: 0.3rem; /* Rounded corners */
  border-bottom-right-radius: 0.3rem;
  transition: opacity 0.3s ease; /* Smooth fade-in effect */
  opacity: 0; /* Initially hidden */
  padding: 0 1.25rem;
  margin: 0rem;

  list-style: none;
}

/* Dropdown links */
.dropdown-link {
  width: 100%; 
  color: #fff;
  background: none;
  padding: 0.3rem 0.5rem;
  text-decoration: none;
  display: block;
  text-align: left;
  font-size: 1.2rem;
  font-family: var(--font-primary);
  transition: background-color 0.3s ease; /* Smooth background color change */
}

/* Show dropdown content on hover */
.nav-item.dropdown:hover .dropdown-content {
  opacity: 1; /* Show the dropdown with a fade-in effect */
}

/* Dropdown link hover effect */
.dropdown-link:hover {
  text-decoration: underline;
}

/* Responsive dropdown menu for smaller screens */
@media screen and (max-width: 960px) {
  .nav-menu {
    grid-template-columns: 1fr; /* Single column layout for dropdown */
  }

  .dropdown-content {
    position: relative; /* Adjust positioning for mobile view */
    top: auto;
    min-width: 100%; /* Full width for dropdown in mobile view */
    box-shadow: none;
    border-radius: 0;
  }
}


@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    cursor: pointer;
    color: #fff; /* Adjust color to ensure visibility */
  }

}